//card used for represent Date,Time,Fuel Level, and Fuel Hight 
//card component use in user dashboard
import React from 'react';
import Aux from '../../hoc/AuxHoc';
import './CardsValue.css'; 
import { Button } from 'antd';
const CardsValue =(props)=>(

    <Aux>
        <div className = "card card-2">
        <div className="flex-container">
            <div>
            <h4>Date = {props.Date}</h4>  
            </div>
            <div>
                <h4>Time = {props.Time}</h4> 
            </div>
            <div>
            <Button onClick={()=>props.Download()} > Download</Button>
            </div>  
            <div>
            <h4>Fuel Hight = {props.FuelHight}</h4>  
            </div>

 
        </div>

         </div>
    </Aux>
);

export default CardsValue;