//user dashboard four cards
import React,{Component} from 'react';
import './DashBoard.css';
import Aux from '../../hoc/AuxHoc';
import Card from '../../components/Cards/Cards'
import CardsValue from '../../components/CardsValue/CardsValue';
import { Button } from 'antd';

class DashBoard extends Component {
  constructor(props) {
    super(props);
    this.state={
        Day: null,
        FuelHight:null ,
        FuelLevel: null,
        Hours: null,
        Lat: null,
        Lon: null,
        Minutes: null,
        Month: null,
        Seconds: null,
        Year: null,
        _id:null
    }
    this.handleChange=this.handleChange.bind(this)
    this.DownloadVehicleData= this.DownloadVehicleData.bind(this)
  }
  componentDidUpdate(){
    if(this.props.values.vehicle_registration_number_Update){
        this.handleChange()
        this.props.vehicle_Update()
    }
  }
  async handleChange() {
  await this.props.values.vehicle_registration_number.locations.map((vehicle)=>{
         this.setState(
           {Day:vehicle.Day,
           FuelHight:vehicle.FuelHight,
           FuelLevel:vehicle.FuelLevel,
           Hours:vehicle.Hours,
           Lat:vehicle.Lat,
           Lon:vehicle.Lon,
           Minutes:vehicle.Minutes,
           Month:vehicle.Month,
           Seconds:vehicle.Seconds,
           Year:vehicle.Year,
          _id:vehicle._id}
          )
      })

    }
  
  async DownloadVehicleData(){
        console.log(this.props.values.vehicle_registration_number)
        const { Parser } = require('json2csv');
        var fileDownload = require('js-file-download');
        const fields = ['Day', 'FuelHight', 'FuelLevel','Hours','Lat','Lon','Minutes','Month','Seconds','Year'];
         const opts = { fields };
   
         try {
           const parser = new Parser(opts);
           const csv = parser.parse(this.props.values.vehicle_registration_number.locations);
           fileDownload(csv, `${this.props.values.vehicle_registration_number.vehicle_registration_number}.csv`);
           console.log(csv);
         } catch (err) {
           console.error(err);
         }
  }
  render(){
    return(
     <Aux>
      <div style={{display: 'flex', justifyContent: 'center'}}>
      <Card Name= {"FUEL LEVEL"} Level={this.state.FuelLevel}  majorUnit={1} max={5} minorUnit={1} ranges= {[
            { from: 2, to: 3, color: '#ffc700' },
            { from: 3, to: 4, color: '#ff7a00' },
            { from: 4, to: 5, color: '#c20000' }
        ]}/>
      <Card Name= {"MILEAGE"} Level={this.state.FuelHight}  majorUnit={20} max={180} minorUnit={5}  ranges= {[
            { from: 80, to: 120, color: '#ffc700' },
            { from: 120, to: 150, color: '#ff7a00' },
            { from: 150, to: 180, color: '#c20000' }
        ]}/>
      <Card Name= {"SPEED"} Level={this.state.FuelHight} majorUnit={20} max={180}  minorUnit={5}  ranges= {[
            { from: 80, to: 120, color: '#ffc700' },
            { from: 120, to: 150, color: '#ff7a00' },
            { from: 150, to: 180, color: '#c20000' }
        ]}/>
      <CardsValue 
      Name= {"SPEED"} 
      FuelLevel={this.state.FuelLevel}
      FuelHight={this.state.FuelHight}
      Date={this.state.Year+"/"+this.state.Month+"/"+this.state.Day}
      Time={this.state.Hours+":"+this.state.Minutes+":"+this.state.Seconds}
      Download={this.DownloadVehicleData}
       />
      
      </div>
     </Aux>
    )
  }
}



export default DashBoard;