//navigation header 
//admin dashboard
import React,{Component} from 'react';
import Aux from '../../hoc/AuxHoc';
import './heder.css';
import {Link} from "react-router-dom";
import { Button } from 'antd';
import trackFluSensor from '../../Apis/trackFluSensor';
import { withRouter } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();
class Heder extends Component{
    constructor(props) {
        super(props);
  
     this.signOut=this.signOut.bind(this)
      }
      async signOut(){
        try{
          const JWD = cookies.get('JWD')
        const headers = {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${JWD}` 
        }

          //const response =  await trackFluSensor.get("/Admins/me",config);
          const response =  await trackFluSensor.get("/Admins/LogOut", {
            headers: headers
          });
          const JWDStatus = response.data.adminStatus
          if(!JWDStatus){
            this.props.history.push('/');
          }
        }catch(e){
    
        } 

    }
 render(){
    return (<Aux>
        <ul>
        <li><Link className="Link"  to="/AdminDashBoard">Admin</Link></li>
        <li><Link className="Link"  to="/AdminInformation" >Admin Information</Link></li>
        <li><Link className="Link"  to="/UserInformation" >User Information</Link></li>
        <li className="Link"  style={{ float: "right"}}>  <Button onClick={()=>this.signOut()} > SignOut</Button></li>
      
        </ul>

    </Aux>)

    }
};

export default withRouter(Heder);